
.AllElements,
.AllMagic,
.CoreElements,
.MagicBuilder,
.MixedMagic,
.PrimeElements,
.PureMagic,
.Reviews,
.SingleElement,
.AllClasses,
.SingleClass,
.BasicClass,
.AdvancedClass,
.MasterClass {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.AllElements h2,
.AllMagic h2,
.CoreElements h2,
.MagicBuilder h1,
.MagicBuilder h2,
.MixedMagic h2,
.PrimeElements h2,
.PureMagic h2,
.Reviews h2,
.SingleElement h2 {
  font-size: 24px;
  font-family: "Helvetica Nue";
  margin-bottom: 20px;
  background: linear-gradient(45deg, #ff6b6b, #556270);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: gradient-animation 5s ease infinite;
}

@keyframes gradient-animation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}


.all-classes-container,
.all-magic-container,
.all-elements-container {
 
}

.class-grid,
.magic-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
}

.class-card {
}

.header-container {
  width: 100%;
  display: flex;
  justify-content: center;
}

.AllElements a,
.AllMagic .magic-link,
.CoreElements a,
.PrimeElements a {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-decoration: none;
  color: #fff;
  margin-bottom: 20px;
  transition: transform 0.3s ease;
}

.AllElements a:hover,
.AllMagic .magic-link:hover,
.CoreElements a:hover,
.PrimeElements a:hover {
  transform: scale(1.1);
}

.AllElements img,
.AllMagic .magic-item img,
.CoreElements img,
.MixedMagic .magic-item img,
.PrimeElements img,
.PureMagic .magic-item img,
.SingleElement img {
  width: 200px;
  height: 200px;
  object-fit: cover;
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.AllMagic,
.MixedMagic .magic-item,
.PrimeElements,
.PureMagic .magic-item {
  padding: 20px;
  background-color: #333;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.MixedMagic .magic-item h3,
.PrimeElements p,
.PureMagic .magic-item h3,
.PureMagic .magic-item p {
  margin-top: 10px;
  font-size: 20px;
  font-weight: bold;
}

.MagicBuilder button,
.Reviews button {
  padding: 10px 20px;
  font-size: 18px;
  background-color: #333;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.Reviews form,
.Reviews ul {
  margin-bottom: 20px;
}

.Reviews textarea {
  width: 100%;
  padding: 10px;
  font-size: 18px;
  margin-bottom: 10px;
  border-radius: 10px;
}

.Reviews ul {
  list-style: none;
  padding: 0;
}

.Reviews li {
  display: flex;
  align-items: flex-start;
  margin-bottom: 32px;
}

.Reviews li img {
  width: 60px;
  height: 60px;
  object-fit: cover;
  border-radius: 50%;
  margin-right: 10px;
}

.Reviews li p {
  font-size: 16px;
}


@media (max-width: 768px) {
  .AllElements img,
  .AllMagic .magic-item img,
  .CoreElements img,
  .MagicBuilder img,
  .MixedMagic .magic-item img,
  .PrimeElements img,
  .PureMagic .magic-item img,
  .SingleElement img {
    width: 150px;
    height: 150px;
  }

  .AllElements h2,
  .AllMagic h2,
  .CoreElements h2,
  .MixedMagic h2,
  .PrimeElements h2,
  .PureMagic h2,
  .Reviews h2,
  .SingleElement h2 {
    font-size: 20px;
  }

  .MagicBuilder label {
    font-size: 16px;
  }

 
  .MagicBuilder select {
    font-size: 14px;
  }

  
  .MagicBuilder button,
  .Reviews button {
    font-size: 16px;
  }
}
.homepage-container {
  padding: 0px;
  box-sizing: border-box;
}

.content-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0px;
  box-sizing: border-box;
}
